.knopka {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    transition: all 1s;

}

.knopka.active {
    background: transparent;
    background: rgba(0, 0, 0, 0.3);;

}

.App {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-100%);
    box-shadow: 0px -0.11px 16.9495px rgba(183, 187, 225, 0.33);
    transition: all 1s;

}

.App.active {
    transform: translateY(0);
}

.modal {
    margin: 5%;
    width: 552px;
    height: auto;
    padding-bottom:5% ;
    left: 444px;
    top: 120px;

    background: #FFFFFF;
    border-radius: 30px;
}

.button {
    display: flex;
    flex-direction: row;
    padding: 16px 32px;
margin: auto;

    /* White */

    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    filter: drop-shadow(0px 0px 44px #CFDAE7);
    border-radius: 6px;
    background: transparent;
    color: white;
}

.button:hover {
    background-color: white;
    color: black;
}


.modal-title {
    margin: 5%;

    /* Desktop/H3 */

    font-family: Lab Grotesque;
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 40px;
    /* identical to box height, or 143% */


    color: #000000;
}

.modal-description {
    margin: 5%;

    /* Desktop/Body 14 */

    font-family: Lab Grotesque, serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    /* or 171% */


    color: #808080;
}

.modal-input {
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 5%;
    width: 95%;
}

.modal-input-set {
    top: 44.44%;
    width: 92%;
    height: 30px;
    border: 1px solid #DFE3E6;
    box-sizing: border-box;
    border-radius: 3px;
}

.modal-input-title {
    font-family: Lab Grotesque;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    /* identical to box height, or 171% */


    color: #000000;

}

.math {
    margin-left: 5%;
    margin-top: 2%;
    font-family: Lab Grotesque;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    /* identical to box height, or 171% */


    /* Red */

    color: #EA0029;
}
.close{
    margin: 3%;
    position: fixed;
    right:15%
}
.switcher {
    margin: 5%;
}
.for-checkbox{
color: yellow;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    border-radius: 6px;
}
.button:checked {
    animation: popup 1 2s normal;
}
.switcher-var{
    align-items: center;
    margin-right: 16px;
    padding: 6px 12px;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    border-radius: 50px;
}
.switcher-var.active{
    background: #EEF0F2;
    border-radius: 50px;
}